import withReactContent from 'sweetalert2-react-content'

// ** Third Party Components
import Swal from 'sweetalert2'
import { store } from '@store/store'
import {removeBanner } from './store/bannerSlice'
const MySwal = withReactContent(Swal)

const handleRemove = async (id) => {
  try {
    await store.dispatch(removeBanner(id))
    return true
  } catch (err) {
    return false
  }
}
export const handleRemoveEmployee = (id) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert Promotion!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, Remove Promotion!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async function (result) {
      if (result.value) {
        const response = handleRemove(id)
        if (response) {
          MySwal.fire({
            icon: 'success',
            title: 'Removed!',
            text: 'Promotion has been removed.',
            customClass: {
              confirmButton: 'btn btn-success'
            }
          })
        }
        
      } else if (result.dismiss === MySwal.DismissReason.cancel) {
        MySwal.fire({
          title: 'Cancelled',
          text: 'Cancelled Removing Promotion :)',
          icon: 'error',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }
