// ** User List Component
import Table from './Table'

// ** Reactstrap Imports
import { Row, Col } from 'reactstrap'

// ** Custom Components
import StatsHorizontal from '@components/widgets/stats/StatsHorizontal'

// ** Icons Imports
import {Gift} from 'react-feather'

// ** Styles
import '@styles/react/apps/app-users.scss'
import { useSelector } from 'react-redux'

const UsersList = () => {
  const store = useSelector(state => state.banners)
  const products = store.banners
  const activeProducts = products.filter(product => product.status === true)
  return (
    <div className='app-user-list'>
      <Row>
      <Col lg='4' sm='6'>
          <StatsHorizontal
            color='primary'
            statTitle='Total Banners'
            icon={<Gift size={20} />}
            renderStats={<h3 className='fw-bolder mb-75'>{products.length}</h3>}
          />
        </Col>
        <Col lg='4' sm='6'>
          <StatsHorizontal
            color='success'
            statTitle='Running Banners'
            icon={<Gift size={20} />}
            renderStats={<h3 className='fw-bolder mb-75'>{activeProducts.length}</h3>}
          />
        </Col>
        <Col lg='4' sm='6'>
          <StatsHorizontal
            color='danger'
            statTitle='InActive Banners'
            icon={<Gift size={20} />}
            renderStats={<h3 className='fw-bolder mb-75'>{products.length - activeProducts.length}</h3>}
          />
        </Col>
      </Row>
      <Table />
    </div>
  )
}

export default UsersList
