export const roleText = (role) => {
    switch (role) {
        case 0:
          return "User"
        case 1:
          return "Admin"
        case 2:
          return "SuperAdmin"
      }
}

export const  statusText = (status) => {
    if (status) {
      return "active"
    } else {
      return "inactive"
    }
  }