// ** React Imports
import { Link, NavLink } from 'react-router-dom'

// ** Icons Imports
import { MoreVertical, Trash2, Archive, Trash } from 'react-feather'
import Avatar from '@components/avatar'
// ** Reactstrap Imports
import { Badge, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap'

import { handleRemoveEmployee } from './delete'
import { statusText } from '../../../functions/customfunctions'

// ** Renders Status Columns

const statusObj = {
  active: 'light-success',
  inactive: 'light-secondary',
  expired: 'light-danger'
}

export const columns = [
  {
    name: 'image',
    minWidth: '130px',
    sortable: true,
    sortField: 'image',
    selector: row => row.imageUrl,
    cell: row => <Avatar className='me-1' img={row.imageUrl} width='50' height='50' />
  },
  {
    name: 'Title',
    sortable: true,
    minWidth: '200px',
    sortField: 'Title',
    selector: row => row.title,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>
        <div className='d-flex flex-column'>
          {row.title}
        </div>
      </div>
    )
  },
  {
    name: 'Description',
    sortable: true,
    minWidth: '200px',
    sortField: 'Description',
    selector: row => row.Description,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>
        <div className='d-flex flex-column'>
          {row.description}
        </div>
      </div>
    )
  },
  {
    name: 'Status',
    minWidth: '138px',
    sortable: true,
    sortField: 'status',
    selector: row => row.status,
    cell: row => (
      <Badge className='text-capitalize' color={statusObj[statusText(row.status)]} pill>
        {statusText(row.status)}
      </Badge>
    )
  },
  {
    name: 'Actions',
    minWidth: '100px',
    cell: row => (
      <div className='column-action'>
        <div  style={{cursor: "pointer"}}
              tag={Link}
              className='w-100'
              onClick={e => {
                e.preventDefault()
                handleRemoveEmployee(row._id)
                // store.dispatch(deleteUser(row._id))
              }}
            >
              <Trash size={14} className='me-50' />
              
          </div>
      </div>
    )
  }
]
